@import '../global.css';
#mainFooter {
    min-height: 300px;
    padding: 50px 50px 10px 50px;
    z-index: 999;
    background: var(--color-bg);
    position: relative;
}
#mainFooter p.footer--v {
    text-align: center;
    margin-top: 50px;
}
.container--footer {
    display: grid;
    gap: 50px;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;

}

#mainFooter .c_2, #mainFooter .c_3 {
    display: flex;
    flex-direction: column;
    gap: 15px;
}
#mainFooter a:not(.logo) {
    color: var(--color-primary--var-2);
}
#mainFooter a:hover:not(.logo) {
    color: var(--color-primary);
}