@import '../../styles/global.css';
#s_addItem form, #s_addTag form {
    display: flex;
    flex-direction: column;
}
#s_addItem form input, #s_addItem form textarea, #s_addItem form select, #s_addTag form select {
    padding: 10px 15px;
    margin-bottom: 25px;
    border-radius: 3px;
    border: 1px solid #333;
    resize: vertical;
}
#html, #js, #css, #code {
    background: #333;
    color: #fff;
    border: 1px solid #fff !important;
    height: 100px !important;
}
#s_addItem label, #s_addTag label {
    margin-bottom: 5px;
}
#s_addTag form button, #s_addItem form button {
    position: relative;
    width: 100px;
    padding: 15px 5px;
    cursor: pointer;
    color: var(--color-text);
    border: 1px solid var(--color-text);
    background: none;
    z-index: 1;
}
#s_addTag form button::before, #s_addItem form button::before {
    content: '';
    position: absolute;
    width: 0%;
    height: 100%;
    top: 0;
    left: 0;
    background: var(--color-primary);
    z-index: -1;
    transition: width .2s ease-in-out;
}
#s_addTag form button:hover::before, #s_addItem form button:hover::before {
    width: 100%;
}