@import '../../styles/global.css';

#s_login {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#box_login {
    display: flex;
    flex-direction: column;
    gap: 80px;
    width: 500px;
    padding: 25px;
    background: var(--color-text);
    color: var(--color-bg);
    border-radius: 5px;
}
.box_entry {
    display: flex;
    flex-direction: column;
}