@import url('../../styles/global.css');
.mainNav--admin--active, .mainNav--admin {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    padding: 25px;
    border-right: 1px solid #cbcbcb;
    background: #fff;
    color: var(--color-bg);
}
.mainNav--admin {
    width: 280px;
    transition: width .3s ease-in-out;
}

.mainNav--admin--active {
    width: 110px;
    transition: width .3s ease-in-out;
}
.main--admin--active ul i {
    color: #171717;
}
.mainNav--admin--active ul, .main--admin ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
}
.mainNav--admin--active li span {
    display: none;
}
.mainNav--admin ul, .mainNav--admin--active ul {
    margin-top: 60px;
}
.mainNav--admin ul i {
    font-size: 25px;
    margin-right: 15px;
}

.main--admin {
    margin-left: 260px;
    transition: margin-left .3s ease-in-out;
}
.main--admin--active {
    margin-left: 80px;
    transition: margin-left .3s ease-in-out;
}
/* #4880FF */
.mainNav--admin .mnv_links li a {
    width: 200px;
    padding: 15px 25px;
    border-radius: 5px;
    font-size: 14px;
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 25px;
}
.mainNav--admin li .item_nav {
    color: #333;
}
.mainNav--admin li .item_nav--active {
    background: #4880FF;
    color: #fff;
}
.mnv_links--active i {
    font-size: 30px;
    padding: 5px;
}
.mnv_links--active .item_nav--active i {
    background: #4880FF;
    color: #fff;
    border-radius: 50%;
}

.mainNav--admin li .item_nav--active::before {
content: '';
position: absolute;
width: 30px;
height: 100%;
top: 0;
left: -60px;
background: #4880FF;
border-radius: 5px;
}

.btn_hamb--active, .btn_hamb {
    background: none;
    border: none;
    position: relative;
    height: 30px;
    width: 30px;
    padding: 0;
    left: 50%;
    transform: translateX(-50%);
}
.btn_hamb--active .line, .btn_hamb .line {
    width: 100%;
    height: 3px;
    background: #333;
}
.btn_hamb--active .line:nth-child(1), .btn_hamb .line:nth-child(1){
    position: absolute;
    top: 0;
}
.btn_hamb--active .line:nth-child(2), .btn_hamb .line:nth-child(2){
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.btn_hamb--active .line:nth-child(3), .btn_hamb .line:nth-child(3){
    position: absolute;
    bottom: 0;
}

.mnv--logo {
    width: 80px;
    margin-bottom: 50px;
}
.main--admin .logo, .main--admin--active .logo {
    font-size: var(--fz-h6);
    color: var(--color-bg);
}