#mainNav {
    width: 100%;
    padding: 15px 50px;
    top: 0;
    position: fixed;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 100px;
    background: var(--color-bg);
}
#mainNav ul {
    display: flex;
    gap: 50px;
}
#mainNav a {
    position: relative;
    /* padding: 5px; */
    z-index: 1;
}
#mainNav .item_nav--active::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 50%;
    bottom: 0;
    left: 0;
    background: var(--color-primary--var-1);
    z-index: -1;
}
.mainNavRS {
    display: flex;
    align-items: center;
}

.mainNavRS a i {
    font-size: var(--fz-h5);
}
.mainNavRS a img {
    height: 25px;
}