@import '../global.css';
.item_card {
    width: 350px;
  }
  #item_large {
    width: 100%;
    height: 500px;
  }
  #item_lage .box_iframe {
    width: 100%;
  }
  
  .item_card--body {
    background: var(--color-text);
    padding: 20px;
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    gap: 10px;
  }
  .item_card--header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
 
  .item_card--body:hover {
    box-shadow: 0 6px 20px rgba(255, 255, 255, 0.1);
  }
  
  .ic--title {
    font-size: var(--fz-p);
    color: var(--color-bg);
    margin-bottom: 0.5rem;
    text-transform: capitalize;
  }
  .ic--title span {
    color: var(--color-text--var-1);
    font-size: 1.4rem;
  }
  .ic--cate {
    font-size: 1.4rem;
    background: var(--color-primary);
    padding: 5px 10px;
    border-radius: 25px;
  }
  

  .ic--desc {
    color: #666;
    font-weight: normal;
  }
  .box_iframe {
    position: relative;
    width: 100%;
  }
  .box_iframe iframe {
            width: 100%;
        height: 300px;
        border: 1px solid #ccc;
        border-radius: 8px;
        margin-top: 1rem;
  }
  .item_card #btn_copy {
    color: initial;
    position: absolute;
    bottom: 20px;
    right: 15px;
    display: flex;
    align-items: center;
    gap: 10px;
    background: var(--color-text);
    padding: 5px 10px;
    border-radius: 25px;
    transition: all .2s ease-in;
  }
  .item_card #btn_copy:hover {
    color: var(--color-text);
    background: var(--color-bg);
  }
  

  
  .item_card .author {
    display: block;
    margin-top: 2rem;
    font-size: var(--fz-p);
  }
  
  .item_card .author--link {
    color: var(--color-primary);
    text-decoration: none;
  }
  
