@import '../../styles/global.css';

.statBox {
    background: var(--color-text);
    color: var(--color-bg);
    width: 300px;
    height: 200px;
    padding: 15px;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: baseline;
}
.statBox i {
    font-size: var(--fz-h4);
    color: var(--color-text);
    background: var(--color-primary);
    padding: 15px;
    border-radius: 50%;
}
.title--admin {
    font-weight: 500;
    font-size: var(--fz-h5);
}
.st--name {
    color: var(--color-text--var-1);
    font-weight: normal;
    font-size: var(--fz-p);
}
.st--number {
    font-size: var(--fz-h5);
}
.container-statBox {
    display: flex;
    gap: 25px;
}