.itemNav {
    position: fixed;
    width: 240px;
    height: 100vh;
    padding: 150px 20px;
    /* border-right: 1px solid var(--color-text); */
    font-size: 14px;
    z-index: 1;
}
.itemNav i {
    margin-right: 10px;
}
.itemNav li {
    width: 100%;
    margin-bottom: 50px;
}
.itemNav li a {
    align-items: center;
    display: flex;
    width: 100%;
    padding: 8px;
    transition: all .3s ease-in-out;

}
.itemNav li a.item_itemNav--active, .itemNav li a:hover{
    background: #2e2e2e;
    border-radius: 5px;
}

#s_listItem {
    padding-left: 300px;
    min-height: 100vh;
}
#s_listItem h1 {
    margin-bottom: 50px;
}
.container_itemCard {
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
}
#s_listItem #tag {
    min-width: 100px;
    margin-left: 10px;
    background: var(--color-bg);
    color: var(--color-text);
    padding: 5px 15px;
    text-transform: capitalize;
    border: none;
    border-bottom: 1px solid var(--color-text);
    margin-bottom: 25px;
}