@import '../global.css';
.cc_result iframe {
    width: 100%;
    border: none;
    height: 300px;
}
.cc_code {
    width: 100%;
    min-height: 500px;
    background: #383838;
    padding: 25px 50px;
}
#codeNav ul {
    display: flex;
    gap: 20px;
    margin-bottom: 30px;
    list-style: none;
    padding: 0;
  }
  
  #codeNav ul li button {
    background: var(--color-bg);
    border: none;
    color: white;
    font-size: 1.4rem;
    padding: 10px 16px;
    display: flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    border-radius: 6px;
    transition: all 0.2s ease;
  }
  
  #codeNav ul li button i {
    font-size: 1.4rem;
  }
  
  #codeNav ul li button:hover {
    background-color: #3a3a3a;
    transform: translateY(-2px);
  }

  #codeNav ul.cn_btn_html li:nth-child(1) button i {
    color: #FF3336;
  }
  #codeNav ul.cn_btn_css li:nth-child(2) button i {
    color: #427AD4;
  }
  #codeNav ul.cn_btn_js li:nth-child(3) button i {
    color: #F7E01D;
  }
  #codeNav ul.cn_btn_comment li:nth-child(4) button i {
    color: #28a745;
  }
  
.cc_code {
    position: relative;
}
.cc_code #btn_copy {
    position: absolute;
    top: 25px;
    right: 25px;
}
.cc_code pre {
    background-color: #1e1e1e;
    padding: 20px;
    border-radius: 6px;
    border-left: 4px solid var(--color-primary);
    overflow-x: auto;
    margin: 0;
  }