#responsve--message {
    width: 100%;
    height: 100vh;
    background: hsla(0, 0%, 0%, 0.95);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999999;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: none;
    text-align: center;
}
#responsve--message .message {
    font-size: 2rem;
}
@media screen and (max-width: 768px) {
    #responsve--message {
        display: flex;
    }   
}